import React, { useState, useEffect, Fragment } from 'react'
import config from './../../../config';


import TextField from '@material-ui/core/TextField';
import CircularProgress from '@material-ui/core/CircularProgress';
import { onUpdateObservationProject } from '../../../api/api';
import { ToastContainer, toast } from 'react-toastify';
import { Button } from '@material-ui/core';

const ProjectInfo = props => {

    const [project, setProject] = useState(null);
    const [observations, setObservations] = useState('');
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (props.project) {
            setProject(props.project);
            setObservations(props.project.observation)
        }
    }, [project, props.project]);

    const updateObservation = () => {
        setLoading(true);
        let data = {
            id: project._id,
            observations
        }
        onUpdateObservationProject(data).then(response => {
            setLoading(false);
            if (response.status == 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        })
    }
    const onChangeDescription = e => {
        setObservations(e.target.value);
    }

    return (
        <Fragment>
            <ToastContainer />
            <div className='project__info'>
                {
                    project ?
                        <Fragment>
                            <div className='project_name'>{project.name}</div>
                            <div className='project_type'>{project.type === 'tri' ? 'Trifásico' : 'Monofásico'}</div>
                            <div className='board_observation'>
                                <Fragment>
                                    <TextField
                                        autoComplete='off'
                                        multiline
                                        margin="dense"
                                        label="Observaciones"
                                        value={observations}
                                        onChange={onChangeDescription}
                                        fullWidth
                                    />
                                    {
                                        loading ?
                                            <Fragment>
                                                <CircularProgress />
                                            </Fragment> :
                                            <Button onClick={updateObservation} color="primary">
                                                Actualizar Observaciones
                                            </Button>
                                    }
                                </Fragment>
                            </div>
                        </Fragment>
                        : ''
                }
            </div>
        </Fragment>
    )
}

export default ProjectInfo