import React, { useState, useEffect, Fragment } from 'react';
import TextField from '@material-ui/core/TextField';
import CircularProgress from '@material-ui/core/CircularProgress';
import config from './../../../config';
import {
    Card,
    CardHeader,
    CardContent,
    Divider,
    Table,
    TableBody,
    TableRow,
    TableCell,
    Button,
} from '@material-ui/core';
import ImageDetail from './../ImageDetail';
import { BiShowAlt } from 'react-icons/bi';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { ToastContainer, toast } from 'react-toastify';
import { Zoom } from "react-slideshow-image";
import Chip from '@material-ui/core/Chip';
import Slide from '@material-ui/core/Slide';
import { openItemBoard, onUpdateObservationBoard } from '../../../api/api';
import AliceCarousel from 'react-alice-carousel';
import 'react-alice-carousel/lib/alice-carousel.css';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})


const ProjectInfo = props => {

    const [project, setProject] = useState(null);
    const [openDetailImage, setOpenDetailImage] = useState(false);
    const [url, setUrl] = useState('');
    const [observations, setObservations] = useState('');
    const [loading, setLoading] = useState(false);
    const [boards, setBoards] = useState([]);
    const [type, setType] = useState("");
    const [board, setBoard] = useState(null);
    const [cellsBefore, setCellsBefore] = useState([]);
    const [cellsVoltaje, setCellsVoltaje] = useState([]);
    const [cellsCorriente, setCellsCorriente] = useState([]);
    const [cellsAfter, setCellsAfter] = useState([]);
    const [cellsFinding, setCellsFinding] = useState([]);

    useEffect(() => {
        setProject(props.project);
        if (props.project) {
            setBoards(props.project.boards);

        }
    }, [type, project, props.project]);

    const updateObservation = () => {
        console.log(observations);
        setLoading(true);
        let data = {
            id: board._id,
            observations
        }
        onUpdateObservationBoard(data).then(response => {
            setLoading(false);
            if (response.status == 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        })
    }

    const onSelectBoard = board => _ => {
        console.log(board);
        setObservations(board.observations);
        let newItemsBoard = board.itemsBoards.map(item => {
            if (item.photos.length === 0) {
                item.photos = [{
                    url: 'default.png',
                    type: 'remote'
                }];
            }

            return item
        });
        board.itemsBoards = newItemsBoard;
        setBoard(board);
        setObservations(board.observation)
        console.log(board.observation);

        let tmpCellsBefore = board.itemsBoards.filter(itemBoard => itemBoard.item.mode == 'before')
        const newCellBefore = tmpCellsBefore.map(cell => {
            const newPhotos = cell.photos.map(photo => {
                console.log(photo)
                if (!photo.props) {
                    return <div className="item" data-value="1">
                        <img
                            src={`${config.urlImages}${photo.url}`}
                            onClick={showImages(`${config.urlImages}${photo.url}`)}
                        />
                    </div>
                } else {
                    return photo;
                }
            });
            cell.photos = newPhotos;

            return cell;
        });
        setCellsBefore(newCellBefore);


        let tmpCellsVoltaje = board.itemsBoards.filter(itemBoard => itemBoard.item.type == 'voltaje')
        const newCellVoltaje = tmpCellsVoltaje.map(cell => {
            const newPhotos = cell.photos.map(photo => {
                console.log(photo)
                if (!photo.props) {
                    return <div className="item" data-value="1">
                        <img
                            src={`${config.urlImages}${photo.url}`}
                            onClick={showImages(`${config.urlImages}${photo.url}`)}
                        />
                    </div>
                } else {
                    return photo;
                }
            });
            cell.photos = newPhotos;

            return cell;
        });
        setCellsVoltaje(newCellVoltaje)

        let tmpCellsCorriente = board.itemsBoards.filter(itemBoard => itemBoard.item.type == 'corriente')
        const newCellCorriente = tmpCellsCorriente.map(cell => {
            const newPhotos = cell.photos.map(photo => {
                console.log(photo)
                if (!photo.props) {
                    return <div className="item" data-value="1">
                        <img
                            src={`${config.urlImages}${photo.url}`}
                            onClick={showImages(`${config.urlImages}${photo.url}`)}
                        />
                    </div>
                } else {
                    return photo;
                }
            });
            cell.photos = newPhotos;

            return cell;
        });
        setCellsCorriente(newCellCorriente)

        let tmpCellsAfter = board.itemsBoards.filter(itemBoard => itemBoard.item.mode == 'after')
        const newCellsAfter = tmpCellsAfter.map(cell => {
            const newPhotos = cell.photos.map(photo => {
                console.log(photo)
                if (!photo.props) {
                    return <div className="item" data-value="1">
                        <img
                            src={`${config.urlImages}${photo.url}`}
                            onClick={showImages(`${config.urlImages}${photo.url}`)}
                        />
                    </div>
                } else {
                    return photo;
                }
            });
            cell.photos = newPhotos;

            return cell;
        });

        setCellsAfter(newCellsAfter)

        let tmpCellsFinding = board.itemsBoards.filter(itemBoard => itemBoard.item.mode == 'finding')
        const newCellsFinding = tmpCellsFinding.map(cell => {
            const newPhotos = cell.photos.map(photo => {
                if (!photo.props) {
                    return <div className="item" data-value="1">
                        <img
                            src={`${config.urlImages}${photo.url}`}
                            onClick={showImages(`${config.urlImages}${photo.url}`)}
                        />
                    </div>
                } else {
                    return photo;
                }
            });
            cell.photos = newPhotos;

            return cell;
        });
        setCellsFinding(newCellsFinding)
    }

    const showImages = url => _ => {
        setUrl(url);
        setOpenDetailImage(true);
    }

    const onOpenItemBoard = currentCell => _ => {
        openItemBoard(currentCell._id).then(response => {

            if (response.status === 'success') {
                switch (currentCell.item.mode) {
                    case 'before':
                        let tmpCellsBefore = cellsBefore.map(cell => {
                            console.log('cell ', cell)
                            if (cell._id === currentCell._id) {
                                cell.status = 'activo';
                            }
                            return cell;
                        })
                        setCellsBefore(tmpCellsBefore);
                        break;
                    case 'after':
                        let tmpCellsAfter = cellsAfter.map(cell => {
                            if (cell._id === currentCell._id) {
                                cell.status = 'activo';
                            }
                            return cell;
                        })
                        setCellsAfter(tmpCellsAfter)
                        break;
                }
                switch (currentCell.item.type) {
                    case 'voltaje':
                        let tmpCellsVoltaje = cellsBefore.map(cell => {
                            if (cell._id === currentCell._id) {
                                cell.status = 'activo';
                            }
                            return cell;
                        })
                        setCellsVoltaje(tmpCellsVoltaje);
                        break;
                    case 'corriente':
                        let tmpCellsCorriente = cellsCorriente.map(cell => {
                            if (cell._id === currentCell._id) {
                                cell.status = 'activo';
                            }
                            return cell;
                        })
                        setCellsCorriente(tmpCellsCorriente);
                        break;
                }
            }
        });
    }

    const closeDatailImage = () => {
        setOpenDetailImage(false)
    }

    const onChangeDescription = e => {
        setObservations(e.target.value);
    }



    return (
        <Fragment>
            <ToastContainer />
            <Card>
                <CardHeader title="TABLEROS" />
                <Divider />
                <CardContent >
                    <Table>
                        <TableBody>
                            <TableRow>
                                <TableCell>
                                    <div className='board__item__wrapper'>
                                        {
                                            project ?
                                                project.boards.map((board, i) => {
                                                    return (<div key={i} className='board_item'>
                                                        <Chip
                                                            size='small'
                                                            color='primary'
                                                            label={board.name}
                                                            variant="outlined"
                                                            onClick={onSelectBoard(board)} />
                                                    </div>)
                                                })
                                                : ''
                                        }
                                    </div>

                                    <div className='board_observations'>
                                        <Fragment>
                                            <TextField
                                                autoComplete='off'
                                                multiline
                                                margin="dense"
                                                label="Observaciones"
                                                value={observations}
                                                onChange={onChangeDescription}
                                                fullWidth
                                            />
                                            {
                                                loading ?
                                                    <Fragment>
                                                        <CircularProgress />
                                                    </Fragment> :
                                                    <Button onClick={updateObservation} color="primary">
                                                        Actualizar Observaciones
                                                    </Button>
                                            }
                                        </Fragment>
                                    </div>

                                </TableCell>
                            </TableRow>

                            <TableRow>
                                <TableCell>
                                    <div className='project__wrapper'>

                                        <div className='project__board__wrapper'>
                                            <Fragment>
                                                {
                                                    cellsFinding.length > 0 ?
                                                        <Fragment>
                                                            <div className='title_action'>HALLAZGOS</div>
                                                            <div className='aux_title'><span className='bold'> Titulo:</span> {cellsFinding[0].title}</div>
                                                            <div className='aux_observations'><span className='bold'>Observaciones:</span> {cellsFinding[0].observations}</div>
                                                            <div className='board__item__wrapper'>
                                                                {
                                                                    cellsFinding.map((itemFinding, i) => {
                                                                        return (
                                                                            <div className="card" key={i}>
                                                                                <div className='card_title'>{itemFinding.item.title}</div>
                                                                                {
                                                                                    itemFinding.item.letterOne !== '' ?
                                                                                        <div className='card_colors'>
                                                                                            <div className='card_color_left' style={{ backgroundColor: `#FCF338` }}>{itemFinding.item.colorOne}</div>
                                                                                            <div className='card_color_right'></div>
                                                                                        </div>
                                                                                        : ''
                                                                                }
                                                                                <div className='card_header'>
                                                                                    {
                                                                                        itemFinding.status === 'inactivo'
                                                                                            ?
                                                                                            <div className='show__wrapper'>
                                                                                                <BiShowAlt className='icon' onClick={onOpenItemBoard(itemFinding)} />
                                                                                                <div className='show_text'>Esta marcación se encuentra oculta pulse en el icono para mostrar</div>
                                                                                            </div>
                                                                                            : ''
                                                                                    }
                                                                                </div>
                                                                                <div className='card_body'>
                                                                                    <div className="slide-container">
                                                                                        <AliceCarousel
                                                                                            autoPlay
                                                                                            infinite={true}
                                                                                            mouseTracking
                                                                                            items={itemFinding.photos}
                                                                                            disableButtonsControls={true}
                                                                                            controlsStrategy="alternate"
                                                                                            animationDuration={1000}
                                                                                            autoPlayInterval={Math.random() * (3000 - 500) + 500}
                                                                                        />

                                                                                    </div>
                                                                                </div>
                                                                            </div>
                                                                        )
                                                                    })
                                                                }

                                                            </div>
                                                        </Fragment> : ''
                                                }
                                                {
                                                    cellsBefore.length > 0 ?
                                                        <Fragment>
                                                            <div className='title_action'>ANTES DE MANTENIMIENTO</div>
                                                            <div className='board__item__wrapper'>
                                                                {
                                                                    cellsBefore.map((itemBefore, i) => {
                                                                        return (
                                                                            <div className="card" key={i}>
                                                                                <div className='card_title'>{itemBefore.item.title}</div>
                                                                                {
                                                                                    itemBefore.item.letterOne !== '' ?
                                                                                        <div className='card_colors'>
                                                                                            <div className='card_color_left' style={{ backgroundColor: `#FCF338` }}>{itemBefore.item.colorOne}</div>
                                                                                            <div className='card_color_right'></div>
                                                                                        </div>
                                                                                        : ''
                                                                                }
                                                                                <div className='card_header'>
                                                                                    {
                                                                                        itemBefore.status === 'inactivo'
                                                                                            ?
                                                                                            <div className='show__wrapper'>
                                                                                                <BiShowAlt className='icon' onClick={onOpenItemBoard(itemBefore)} />
                                                                                                <div className='show_text'>Esta marcación se encuentra oculta pulse en el icono para mostrar</div>
                                                                                            </div>
                                                                                            : ''
                                                                                    }
                                                                                </div>
                                                                                <div className='card_body'>
                                                                                    <div className="slide-container">
                                                                                        <AliceCarousel
                                                                                            autoPlay
                                                                                            infinite={true}
                                                                                            mouseTracking
                                                                                            items={itemBefore.photos}
                                                                                            disableButtonsControls={true}
                                                                                            controlsStrategy="alternate"
                                                                                            animationDuration={1000}
                                                                                            autoPlayInterval={Math.random() * (3000 - 500) + 500}
                                                                                        />

                                                                                    </div>
                                                                                </div>
                                                                            </div>
                                                                        )
                                                                    })
                                                                }

                                                            </div>
                                                        </Fragment> : ''
                                                }
                                                {
                                                    cellsVoltaje.length > 0 ?
                                                        <Fragment>
                                                            <div className='title_action'>VOLTAJES</div>
                                                            <div className='board__item__wrapper'>

                                                                {
                                                                    cellsVoltaje.map((itemVoltaje, i) => {
                                                                        return (
                                                                            <div className="card" key={i}>
                                                                                <div className='card_title'>{itemVoltaje.item.title}</div>
                                                                                {
                                                                                    itemVoltaje.item.letterOne !== '' ?
                                                                                        <div className='card_colors'>
                                                                                            <div
                                                                                                className='card_color_left'
                                                                                                style={{
                                                                                                    backgroundColor: `#${itemVoltaje.item.colorOne}`,
                                                                                                    color: `#${itemVoltaje.item.textColorOne}`
                                                                                                }}
                                                                                            >
                                                                                                {itemVoltaje.item.letterOne}
                                                                                            </div>
                                                                                            <div
                                                                                                className='card_color_right'
                                                                                                style={{
                                                                                                    backgroundColor: `#${itemVoltaje.item.colorTwo}`,
                                                                                                    color: `#${itemVoltaje.item.textColorTwo}`
                                                                                                }}
                                                                                            >
                                                                                                {itemVoltaje.item.letterTwo}
                                                                                            </div>
                                                                                        </div>
                                                                                        : ''
                                                                                }
                                                                                <div className='card_header'>
                                                                                    {
                                                                                        itemVoltaje.status === 'inactivo'
                                                                                            ?
                                                                                            <div className='show__wrapper'>
                                                                                                <BiShowAlt className='icon' onClick={onOpenItemBoard(itemVoltaje)} />
                                                                                                <div className='show_text'>Esta marcación se encuentra oculta pulse en el icono para mostrar</div>
                                                                                            </div>
                                                                                            : ''
                                                                                    }
                                                                                </div>
                                                                                <div className='card_body'>
                                                                                    <AliceCarousel
                                                                                        autoPlay
                                                                                        infinite={true}
                                                                                        mouseTracking
                                                                                        items={itemVoltaje.photos}
                                                                                        disableButtonsControls={true}
                                                                                        controlsStrategy="alternate"
                                                                                        animationDuration={1000}
                                                                                        autoPlayInterval={Math.random() * (3000 - 500) + 500}
                                                                                    />

                                                                                </div>
                                                                                {
                                                                                    <div className='card_footer'>
                                                                                        <span className='text_value'>Valor:</span>
                                                                                        {itemVoltaje.item.value}
                                                                                    </div>
                                                                                }
                                                                            </div>
                                                                        )
                                                                    })
                                                                }

                                                            </div>
                                                        </Fragment> : ''
                                                }
                                                {
                                                    cellsCorriente.length > 0 ?
                                                        <Fragment>
                                                            <div className='title_action'>CORRIENTES</div>
                                                            <div className='board__item__wrapper'>

                                                                {
                                                                    cellsCorriente.map((item, i) => {
                                                                        return (
                                                                            <div className="card" key={i}>
                                                                                <div className='card_title'>{item.item.title}</div>
                                                                                {
                                                                                    <div className='card_colors'>
                                                                                        <div
                                                                                            className='card_color'
                                                                                            style={{
                                                                                                backgroundColor: `#${item.item.colorOne}`,
                                                                                                color: `#${item.item.textColorOne}`
                                                                                            }}
                                                                                        >
                                                                                            {item.item.letterOne}
                                                                                        </div>

                                                                                    </div>
                                                                                }
                                                                                <div className='card_header'>
                                                                                    {
                                                                                        item.status === 'inactivo'
                                                                                            ?
                                                                                            <div className='show__wrapper'>
                                                                                                <BiShowAlt className='icon' onClick={onOpenItemBoard(item)} />
                                                                                                <div className='show_text'>Esta marcación se encuentra oculta pulse en el icono para mostrar</div>
                                                                                            </div>
                                                                                            : ''
                                                                                    }
                                                                                </div>
                                                                                <div className='card_body'>
                                                                                    <AliceCarousel
                                                                                        autoPlay
                                                                                        infinite={true}
                                                                                        mouseTracking
                                                                                        items={item.photos}
                                                                                        disableButtonsControls={true}
                                                                                        controlsStrategy="alternate"
                                                                                        animationDuration={1000}
                                                                                        autoPlayInterval={Math.random() * (3000 - 500) + 500}
                                                                                    />

                                                                                </div>
                                                                                {
                                                                                    <div className='card_footer'>
                                                                                        <span className='text_value'>Valor:</span>
                                                                                        {item.item.value}
                                                                                    </div>
                                                                                }
                                                                            </div>
                                                                        )
                                                                    })
                                                                }

                                                            </div>
                                                        </Fragment> : ''
                                                }
                                                {
                                                    cellsAfter.length > 0 ?
                                                        <Fragment>
                                                            <div className='title_action'>DESPUES DE MANTENIMIENTO</div>
                                                            <div className='board__item__wrapper'>
                                                                {
                                                                    cellsAfter.map((item, i) => {
                                                                        return (
                                                                            <div className="card" key={i}>
                                                                                <div className='card_title'>{item.item.title}</div>
                                                                                {
                                                                                    item.item.letterOne !== '' ?
                                                                                        <div className='card_colors'>
                                                                                            <div className='card_color_left' style={{ backgroundColor: `#FCF338` }}>{item.item.colorOne}</div>
                                                                                            <div className='card_color_right'></div>
                                                                                        </div>
                                                                                        : ''
                                                                                }
                                                                                <div className='card_header'>
                                                                                    {
                                                                                        item.status === 'inactivo'
                                                                                            ?
                                                                                            <div className='show__wrapper'>
                                                                                                <BiShowAlt className='icon' onClick={onOpenItemBoard(item)} />
                                                                                                <div className='show_text'>Esta marcación se encuentra oculta pulse en el icono para mostrar</div>
                                                                                            </div>
                                                                                            : ''
                                                                                    }
                                                                                </div>
                                                                                <div className='card_body'>
                                                                                    <div className="slide-container">
                                                                                        <AliceCarousel
                                                                                            autoPlay
                                                                                            infinite={true}
                                                                                            mouseTracking
                                                                                            items={item.photos}
                                                                                            disableButtonsControls={true}
                                                                                            controlsStrategy="alternate"
                                                                                            animationDuration={1000}
                                                                                            autoPlayInterval={Math.random() * (3000 - 500) + 500}
                                                                                        />

                                                                                    </div>
                                                                                </div>
                                                                            </div>
                                                                        )
                                                                    })
                                                                }

                                                            </div>
                                                        </Fragment> : ''
                                                }
                                            </Fragment>
                                        </div>

                                    </div>

                                </TableCell>
                            </TableRow>
                        </TableBody>
                    </Table >
                </CardContent >
                <Dialog
                    fullWidth maxWidth="sm"
                    open={openDetailImage}
                    onClose={closeDatailImage}
                    TransitionComponent={Transition}>
                    <DialogContent>
                        <ImageDetail url={url} />
                    </DialogContent>
                    <DialogActions>
                        <Button autoFocus onClick={closeDatailImage} color="primary">
                            Cerrar
                        </Button>
                    </DialogActions>
                </Dialog>

            </Card >
        </Fragment>
    )
}

export default ProjectInfo