import React, { useState, useEffect, Fragment } from 'react'
import config from './../../../config';

import {
    Table,
    TableBody,
    TableRow,
    TableCell,
    Button
} from '@material-ui/core';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import AliceCarousel from 'react-alice-carousel';
import 'react-alice-carousel/lib/alice-carousel.css';
import Slide from '@material-ui/core/Slide';
import ImageDetail from './../ImageDetail';


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})
const AroundInfo = props => {

    console.log(props.project);
    const [cellsOutletSampling, setCellOutletSampling] = useState([]);
    const [url, setUrl] = useState('');
    const [didMount, setDidMount] = useState(false);
    const [openDetailImage, setOpenDetailImage] = useState(false);


    useEffect(() => {

        const newCellsOutletSampling = props.project.aroundItems.map(outletSampling => {
            const newPhotos = outletSampling.photos.map(photo => {
                console.log(photo)
                if (!photo.props) {
                    return <div className="item" data-value="1">
                        <img
                            src={`${config.urlImages}${photo.url}`}
                            onClick={showImages(`${config.urlImages}${photo.url}`)}
                        />
                    </div>
                } else {
                    return photo;
                }
            });
            outletSampling.photos = newPhotos;

            return outletSampling;
        });
        setCellOutletSampling(newCellsOutletSampling);

    }, []);

    const showImages = url => _ => {
        setUrl(url);
        console.log('Url', url)
        setOpenDetailImage(true);
    }
    const closeDatailImage = () => {
        setOpenDetailImage(false)
    }

    return (
        <Fragment>
            <Table>
                <TableBody>
                    <TableRow>
                        <TableCell>
                            <div className='title_action'>IMAGENES DEL ENTORNO</div>
                        </TableCell>
                    </TableRow>
                    <TableRow>

                        <TableCell>
                            <div className='board__item__wrapper'>

                                {
                                    cellsOutletSampling.map((item, i) => {
                                        return (
                                            <div className="card" key={i}>
                                                <div className='card_title'>{item.item.title}</div>
                                                {
                                                    item.item.letterOne !== '' ?
                                                        <div className='card_colors'>
                                                            <div className='card_color_left' style={{ backgroundColor: `#FCF338` }}>{item.item.colorOne}</div>
                                                            <div className='card_color_right'></div>
                                                        </div>
                                                        : ''
                                                }

                                                <div className='card_body'>
                                                    <div className="slide-container">

                                                        <AliceCarousel
                                                            autoPlay
                                                            infinite={true}
                                                            mouseTracking
                                                            items={item.photos}
                                                            disableButtonsControls={true}
                                                            controlsStrategy="alternate"
                                                            animationDuration={1000}
                                                            autoPlayInterval={Math.random() * (3000 - 500) + 500}
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }

                            </div>

                        </TableCell>
                    </TableRow>
                </TableBody>
            </Table >
            <Dialog
                fullWidth maxWidth="sm"
                open={openDetailImage}
                onClose={closeDatailImage}
                TransitionComponent={Transition}>
                <DialogContent>
                    <ImageDetail url={url} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeDatailImage} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

        </Fragment >
    )
}

export default AroundInfo