import React, { useState, useEffect, Fragment } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';

import Tooltip from '@material-ui/core/Tooltip'

import ProjectDetail from './ProjectDetail';
import ProjectForm from './ProjectForm';
import { Button } from '@material-ui/core';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import Slide from '@material-ui/core/Slide';

import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { listProjects, deleteProject, onSaveObservations, onSendReportProject, onDownloadAttention } from '../../api/api';
import Chip from '@material-ui/core/Chip';
import AddIcon from '@material-ui/icons/Add';
import { AiOutlineComment } from 'react-icons/ai';
import { FiSend, FiSearch } from 'react-icons/fi';

import CircularProgress from '@material-ui/core/CircularProgress';

import Fab from '@material-ui/core/Fab';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './projects.css';
import Swal from 'sweetalert2'
import TableContainer from '@material-ui/core/TableContainer';
import { AiOutlineCloudDownload } from "react-icons/ai";
import config from '../../config';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})

const Projects = props => {

    useEffect(() => {
        console.log(props)
        let downloaded = false
        if (props.location.pathname === "/maintenances-downloaded") {
            downloaded = true
            setDownloaded(true)
        }
        let data = { start: 0, end: rowsPerPage, paginate: true, "source": "web", downloaded }

        listProjects(data).then(response => {
            setProjects(response.projects);
            setTotalProjects(response.count);
        });
    }, []);

    const classes = useStyles()

    const [downloaded, setDownloaded] = useState(false)
    const [projects, setProjects] = useState([])
    const [project, setProject] = useState(null)
    const [totalProjects, setTotalProjects] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);


    const [open, setOpen] = useState(false);
    const [openDetailProject, setOpenDetailProject] = useState(false);
    const [openProjectForm, setOpenProjectForm] = useState(false);

    const [openObservations, setOpenObservations] = useState(false);
    const [observations, setObservations] = useState('');
    const [loading, setLoading] = useState(false);
    const [loadingReport, setLoadingReport] = useState(false);

    const [pdf, setPdf] = useState(false)
    const [dataPdf, setDataPdf] = useState('')


    const handleChangePage = (_, newPage) => {
        setPage(newPage);
        let data = { start: (newPage * rowsPerPage), end: rowsPerPage, paginate: true, downloaded }
        listProjects(data).then(response => {
            setProjects(response.projects);
            setTotalProjects(response.count);
        });
    }

    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value, downloaded }
        listProjects(data).then(response => {
            setProjects(response.projects);
            setTotalProjects(response.count);
        });
    }

    const onCloseDeleteProject = () => {
        setOpen(false);
    }

    const onRefreshProjects = () => {
        listProjects().then(response => {
            setProjects(response.projects);
            setTotalProjects(response.count);
            setRowsPerPage(1)
        });
    }

    const onDeleteProject = id => {
        setOpen(false);

        deleteProject(id).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newProjects = projects.map((currentProject) => {
                    if (id !== currentProject._id)
                        return currentProject

                    currentProject.status = "deleted";
                    return currentProject
                })
                setProjects(newProjects)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {

            toast.error('Ocurrió un error al eliminar el proyecto', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })

    }

    const onDetailProject = project => () => {
        setProject(project)
        setOpenDetailProject(true)
    }

    const onOpenObservation = project => () => {
        setOpenObservations(true);
        setProject(project);
    }

    const closeDatailProject = () => {
        setOpenDetailProject(false)
    }

    const onOpenProjectForm = () => {
        setOpenProjectForm(true)
    }

    const onCloseProjectForm = () => {
        setOpenProjectForm(false)
    }

    const onCloseObservations = () => {
        setOpenObservations(false);
    }

    const saveObservations = () => {
        console.log(observations);
        console.log(project);
        setLoading(true);
        let data = {
            observations,
            id: project._id
        }
        onSaveObservations(data).then(response => {
            setLoading(false);
            setOpenObservations(false);

            if (response.status === 'success') {
                Swal.fire({
                    icon: 'success',
                    title: 'Mensaje',
                    text: response.message,
                    showConfirmButton: false,
                    timer: 3000
                })
            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: response.message,
                    showConfirmButton: false,
                    timer: 3000
                })
            }

        }).catch(e => {
            console.log(e);
            setLoading(false);

        })

    }

    const onClosePdf = () => {
        setPdf(false)
    }

    const sendReportProject = (id, type) => () => {

        const newProjects = projects.map(currentProject => {
            if (currentProject._id === id) {
                if (type == "download")
                    currentProject.downloading = true;
                else
                    currentProject.loading = true;
            }
            return currentProject;
        });

        setProjects(newProjects);

        onSendReportProject({ id, type }).then(response => {
            const newProjects = projects.map(currentProject => {
                if (currentProject._id === id) {
                    if (type == "download")
                        currentProject.downloading = false;
                    else
                        currentProject.loading = false;
                }
                return currentProject;
            });


            setProjects(newProjects);

            if (response.status === 'success') {
                if (type != "download") {
                    Swal.fire({
                        icon: 'success',
                        title: 'Mensaje',
                        text: response.message,
                        showConfirmButton: false,
                        timer: 3000
                    })
                } else {
                    const newProjects = projects.filter(currentProject => currentProject._id != id)
                    setProjects(newProjects);

                    window.open(`${config.urlPdf}${id}.pdf`)
                }

            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: response.message,
                    showConfirmButton: false,
                    timer: 3000
                })
            }

        }).catch(e => {
            console.log(e);
            setLoading(false);

        })

    }

    const onChangeDescription = e => {
        setObservations(e.target.value);

    }



    return (
        <Fragment>
            <ToastContainer />
            <Paper className={classes.paper}>
                <Fab onClick={onOpenProjectForm} variant="extended" color="primary" size="small" className={classes.margin}>
                    <AddIcon className={classes.extendedIcon} />
                    Nuevo mantenimiento
                </Fab>
                <TableContainer component={Paper}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>DESCRIPCIÓN</TableCell>
                                <TableCell>CLIENTE</TableCell>
                                <TableCell>TABLEROS</TableCell>
                                <TableCell>DETALLE</TableCell>
                                <TableCell>ENVIAR INFORME</TableCell>
                                <TableCell>DESCARGAR INFORME</TableCell>
                                <TableCell>OBSERVACIONES</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                projects.map(project => {
                                    return (
                                        <TableRow key={project._id}>
                                            <TableCell>{project.name} </TableCell>
                                            <TableCell>{project.customer.name}</TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <Chip size='small' label={project.boards.length} variant="outlined" />
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <FiSearch className={'icon'} onClick={onDetailProject(project)} />
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                {
                                                    project.loading ?
                                                        <div className='circularProgress'>
                                                            <CircularProgress size={16} />
                                                        </div> :
                                                        <FiSend className={'icon'} onClick={sendReportProject(project._id, "email")} />
                                                }
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <Tooltip title="Descargar" placement="top">
                                                    {
                                                        project.downloading ?
                                                            <div className='circularProgress'>
                                                                <CircularProgress size={16} />
                                                            </div> :
                                                            <div>
                                                                <AiOutlineCloudDownload className={"icon"} onClick={sendReportProject(project._id, "download")} />
                                                            </div>
                                                    }


                                                </Tooltip>
                                            </TableCell>

                                            <TableCell className={classes.iconWidth}>
                                                <AiOutlineComment className={'icon'} onClick={onOpenObservation(project)} />
                                            </TableCell>
                                        </TableRow>
                                    )
                                })
                            }
                        </TableBody>
                    </Table>

                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[10, 20, 100]}
                    component="div"
                    count={totalProjects}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>

            <Dialog
                maxWidth="lg"
                open={pdf}
                onClose={onClosePdf}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle id="draggable-dialog-title">Reporte de servicios</DialogTitle>
                <DialogContent>
                    <iframe src={dataPdf} title="Imprimir" height="900px" width="600px" ></iframe>
                </DialogContent>
                <DialogActions>

                    <Button onClick={onClosePdf} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={open}
                onClose={onCloseDeleteProject}>
                <DialogTitle >
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar esta atención?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteProject} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteProject} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openProjectForm} onClose={onCloseProjectForm} TransitionComponent={Transition}>
                <DialogContent>
                    <ProjectForm onRefreshProjects={onRefreshProjects} onClose={onCloseProjectForm} project={project} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseProjectForm} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth
                fullScreen={true}
                open={openDetailProject}
                onClose={closeDatailProject}
                TransitionComponent={Transition}>
                <DialogContent>
                    <ProjectDetail project={project} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeDatailProject} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>



            <Dialog
                fullWidth={true}
                maxWidth={'sm'}
                open={openObservations}
                onClose={onCloseObservations}>
                <DialogTitle>
                    Observaciones
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <TextField
                            autoComplete='off'
                            multiline
                            autoFocus
                            margin="dense"
                            id="name"
                            label="Observaciones"
                            onChange={onChangeDescription}
                            fullWidth
                        />
                    </DialogContentText>
                </DialogContent>
                <DialogActions>

                    {
                        loading ?
                            <div className='circularProgress'>
                                <CircularProgress />
                            </div> :

                            <Fragment>
                                <Button autoFocus onClick={onCloseObservations} color="primary">
                                    Cancelar
                                </Button>
                                <Button onClick={saveObservations} color="primary">
                                    Guardar
                                </Button>
                            </Fragment>
                    }
                </DialogActions>
            </Dialog>

        </Fragment>)
}

export default Projects