import React, { useState, useEffect, Fragment } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';

import './projects.css';
import 'react-slideshow-image/dist/styles.css';




import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PhoneIcon from '@material-ui/icons/Phone';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import HelpIcon from '@material-ui/icons/Help';
import ShoppingBasket from '@material-ui/icons/ShoppingBasket';
import ThumbDown from '@material-ui/icons/ThumbDown';
import ThumbUp from '@material-ui/icons/ThumbUp';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import ProjectInfo from './pages/ProjectInfo'
import BoardsInfo from './pages/BoardsInfo'
import AroundInfo from './pages/AroundInfo'
import OutletSamplingInfo from './pages/OutletSamplingInfo'
import { BsClipboard } from 'react-icons/bs'
import { AiOutlinePicture } from 'react-icons/ai'
import { FiImage } from 'react-icons/fi'
import { BsInfoCircle } from 'react-icons/bs'


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-force-tabpanel-${index}`}
      aria-labelledby={`scrollable-force-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <div>{children}</div>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-force-tab-${index}`,
    'aria-controls': `scrollable-force-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))



const ProjectDetail = props => {

  const [project, setProject] = useState(null);



  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (_, newValue) => {
    setValue(newValue);
  };

  useEffect(() => {
    setProject(props.project);
  }, [project, props.project]);




  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="on"
          indicatorColor="primary"
          textColor="primary"
          aria-label="scrollable force tabs example"
        >
          <Tab label="Información del proyecto" icon={<BsInfoCircle className='icon' />} {...a11yProps(0)} />
          <Tab label="Tableros" icon={<BsClipboard className='icon' />} {...a11yProps(1)} />
          <Tab label="Fotos del entorno" icon={<AiOutlinePicture className='icon' />} {...a11yProps(2)} />
          <Tab label="Muestreo de tomas" icon={<FiImage className='icon' />} {...a11yProps(3)} />

        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <ProjectInfo project={project} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <BoardsInfo project={project} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <AroundInfo project={project} />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <OutletSamplingInfo project={project} />
      </TabPanel>

    </div>
  );
}

export default ProjectDetail